'use client';

import React, { useEffect, useRef, useState } from "react";
import { Button } from "./button";
import VehicleCard from "./VehicleCard";

type DateRange<T> = [T | null, T | null];
import { Dayjs } from "dayjs";

import { LocalizationProvider } from '@mui/x-date-pickers/LocalizationProvider';
import { AdapterDayjs } from '@mui/x-date-pickers/AdapterDayjs';
import { DateRangePicker } from '@mui/x-date-pickers-pro';

interface HeaderProps {
  onSearchResults: (results: any) => void;
}

const Header: React.FC<HeaderProps> = ({ onSearchResults }) => {
  const [dateRange, setDateRange] = useState<DateRange<Dayjs>>([null, null]);
  const [userLocation, setUserLocation] = useState<{ lat: number; lng: number } | null>(null);
  const [results, setResults] = useState<any>(null);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);

  // Get user's current location
  const getUserLocation = () => {
    if (!navigator.geolocation) {
      alert("Geolocation is not supported by your browser.");
      return;
    }
    navigator.geolocation.getCurrentPosition(
      (position) => {
        setUserLocation({
          lat: position.coords.latitude,
          lng: position.coords.longitude,
        });
        setError(null);
      },
      (err) => {
        setError("Unable to retrieve your location.");
        console.error(err);
      }
    );
  };

  // Fetch cars nearby user location
  const fetchCarsNearby = async () => {
    if (!userLocation || !dateRange[0] || !dateRange[1]) return;

    const pickUpDate = dateRange[0].format("YYYY-MM-DD");
    const dropOffDate = dateRange[1].format("YYYY-MM-DD");
    const pickUpTime = "10:00";
    const dropOffTime = "10:00";

    // Using the same API but with user location for pickup & dropoff
    const url = `https://booking-com15.p.rapidapi.com/api/v1/cars/searchCarRentals?pick_up_latitude=${userLocation.lat}&pick_up_longitude=${userLocation.lng}&drop_off_latitude=${userLocation.lat}&drop_off_longitude=${userLocation.lng}&pick_up_date=${pickUpDate}&drop_off_date=${dropOffDate}&pick_up_time=${pickUpTime}&drop_off_time=${dropOffTime}&driver_age=30&currency_code=USD&location=US`;

    const options = {
      method: "GET",
      headers: {
        "x-rapidapi-host": "booking-com15.p.rapidapi.com",
        "x-rapidapi-key": "60adf17008msh63e4a1af385166bp1c2d84jsn4e4c2a9332dc",
      },
    };

    setLoading(true);
    setResults(null);
    try {
      const res = await fetch(url, options);
      const data = await res.json();
      setResults(data);
      onSearchResults(data);
    } catch (err) {
      console.error("Error fetching rental cars:", err);
      alert("Failed to fetch rental cars.");
    } finally {
      setLoading(false);
    }
  };

  // When user location or dates change, fetch cars
  useEffect(() => {
    if (userLocation && dateRange[0] && dateRange[1]) {
      fetchCarsNearby();
    }
  }, [userLocation, dateRange]);

  return (
    <div className="w-[1240px] min-h-48 relative mx-auto">
      <div className="w-[1240px] left-0 top-0 absolute inline-flex flex-col justify-start items-start">
        <div className="w-[1266px] px-12 py-9 bg-white shadow-[0px_4px_10px_0px_rgba(218,6,6,0.10)] flex flex-col justify-center items-center gap-2.5">
          <div className="inline-flex justify-center items-center gap-32">
            {/* Datepicker */}
            <div className="inline-flex flex-col justify-start items-start">
              <label className="p-2.5 text-neutral-500 text-base font-normal leading-tight">Date Range</label>
              <div className="p-2.5">
                <LocalizationProvider dateAdapter={AdapterDayjs}>
                  <DateRangePicker
                    calendars={1}
                    value={dateRange}
                    onChange={(newValue) => setDateRange(newValue)}
                    slotProps={{
                      textField: { size: "small", className: "bg-white rounded border border-gray-200" },
                    }}
                  />
                </LocalizationProvider>
              </div>
            </div>

            {/* Use My Location button */}
            <Button
              onClick={getUserLocation}
              variant="destructive"
              size="lg"
              className="font-bold px-8"
              disabled={loading}
            >
              {loading ? "Loading..." : "Use My Location"}
            </Button>
          </div>
        </div>

        {/* Errors */}
        {error && <div className="text-red-600 mt-4">{error}</div>}

        {/* Search Results */}
        <div className="w-full mt-8 grid grid-cols-1 sm:grid-cols-2 md:grid-cols-3 gap-6">
          {results && results.data && results.data.length > 0 ? (
            results.data.map((car: any, idx: number) => (
              <VehicleCard
                key={idx}
                name={car.name || "Unknown"}
                type={car.vehicle_type || "Car"}
                price={car.price_total || 0}
                transmission={car.transmission === "automatic" ? "Automat" : "Manual"}
                photo={car.photo}
              />
            ))
          ) : (
            <div className="text-gray-600 col-span-full text-center">
              {loading ? "Searching for cars near you..." : "No cars found. Try changing dates or use the button to get your location."}
            </div>
          )}
        </div>
      </div>
    </div>
  );
};

export default Header;
